import View from '../core/view'
import { ProductSaveData } from '../types'

export default class SellPostView extends View {
    talentSellSaveData: ProductSaveData;
    constructor() {
        super();
        console.log("sell-post")
        const title: HTMLInputElement = document.getElementById('title') as HTMLInputElement;
        if(title.value) {
            this.talentSellSaveData = this.saveDocument();
        } else {
            this.talentSellSaveData = {
                userId: Number(sessionStorage.getItem('userId')),
                title: null,
                categoryId: null,
                serviceInformation: null,
                images: null,
                price: null,
                type: 'talent-sell'
            }
        }

        this.addEvent();
    }

    addEvent() {
        this.titleEvent();
        this.serviceInformationEvent();
        this.imageEvent();
        this.cancleImageEvent();
        this.submitEvent();
    }

    saveDocument(): ProductSaveData {
        const userId: number = Number(sessionStorage.getItem('userId'));
        const title: HTMLInputElement = document.getElementById('title') as HTMLInputElement;
        const category: HTMLSelectElement = document.getElementById('category-list') as HTMLSelectElement;
        const serviceInformation: HTMLTextAreaElement = document.getElementById('service-information') as HTMLTextAreaElement;
        let images: FileList[];
        const imageItems: NodeListOf<HTMLInputElement> = document.querySelectorAll('.images');
        images = Array.from(imageItems).filter(element => element.value != '').map(element => element.files as FileList);
        console.log(images);
        const price: HTMLInputElement = document.getElementById('price') as HTMLInputElement;
        return {
            userId: userId,
            title: title.value,
            categoryId: category.value,
            serviceInformation: serviceInformation.value,
            images: images,
            price: Number(price.value),
            type: 'talent-sell'
        };
    }

    titleEvent() {
        const titleElement: HTMLInputElement = document.getElementById('title') as HTMLInputElement;
        if(titleElement) {
            titleElement.addEventListener('input', () => {
                const titleLength: HTMLElement | null = document.getElementById('title-length');
                if(titleLength) {
                    titleLength.innerHTML = String(titleElement.value.length);
                }
            })
        }
    }

    serviceInformationEvent() {
        const informationElement: HTMLTextAreaElement = document.getElementById('service-information') as HTMLTextAreaElement;
        if(informationElement) {
            informationElement.addEventListener('input', () => {
                const informationLength: HTMLElement | null = document.getElementById('service-information-length');
                if(informationLength) {
                    informationLength.innerHTML = String(informationElement.value.length);
                }
            })
        }
    }

    imageEvent() {
        const first = this.imageObject('first-image', 'first-image-repository', 0)
        first.container.addEventListener('click', () => {
            first.repository.click();
        })
        first.repository.addEventListener('change', (file) => this.imageChangeEvent(file, first.container, first.repository, first.cancleButton));

        const second = this.imageObject('second-image', 'second-image-repository', 1)
        second.container.addEventListener('click', () => {
            second.repository.click();
        })
        second.repository.addEventListener('change', (file) => this.imageChangeEvent(file, second.container, second.repository, second.cancleButton));
        
        const third = this.imageObject('third-image', 'third-image-repository', 2)
        third.container.addEventListener('click', () => {
            third.repository.click();
        })
        third.repository.addEventListener('change', (file) => this.imageChangeEvent(file, third.container, third.repository, third.cancleButton));

        const fourth = this.imageObject('fourth-image', 'fourth-image-repository', 3)
        fourth.container.addEventListener('click', () => {
            fourth.repository.click();
        })
        fourth.repository.addEventListener('change', (file) => this.imageChangeEvent(file, fourth.container, fourth.repository, fourth.cancleButton));

        const fifth = this.imageObject('fifth-image', 'fifth-image-repository', 4)
        fifth.container.addEventListener('click', () => {
            fifth.repository.click();
        })
        fifth.repository.addEventListener('change', (file) => this.imageChangeEvent(file, fifth.container, fifth.repository, fifth.cancleButton));

    }

    imageObject(containerId: string, repositoryId: string, index: number) {
        const container: HTMLElement = document.getElementById(containerId) as HTMLElement;
        const repository: HTMLInputElement = document.getElementById(repositoryId) as HTMLInputElement;
        const cancleButtons: NodeListOf<HTMLElement> = document.querySelectorAll('.sell-post-image-cancel-button') as NodeListOf<HTMLElement>;
        return {
            container: container,
            repository: repository,
            cancleButton: cancleButtons[index]
        };
    }

    imageChangeEvent(image: Event, container: HTMLElement, repository: HTMLInputElement, cancleButton: HTMLElement) {
        if(image) {
            console.log(image);
            console.log(container);
            const reader = new FileReader();
            reader.addEventListener("load", function () {
                console.log(reader.result);
                container.style.backgroundImage = `url(${ reader.result })`;
                console.log(cancleButton);
                cancleButton.classList.remove('visibility-hidden');
            }, false);
            if(repository.files) {
                reader.readAsDataURL(repository.files[0]);    
            }
        }
    }

    cancleImageEvent() {
        const cancleButtons: NodeListOf<HTMLElement> = document.querySelectorAll('.sell-post-image-cancel-button') as NodeListOf<HTMLElement>;
        const imageContainers: NodeListOf<HTMLElement> = document.querySelectorAll('.image-container') as NodeListOf<HTMLElement>;
        const images: NodeListOf<HTMLInputElement> = document.querySelectorAll('.images') as NodeListOf<HTMLInputElement>;
        for(let i=0; i<cancleButtons.length; i++) {
            cancleButtons[i].addEventListener('click', (event) => {
                imageContainers[i].style.backgroundImage = '';
                images[i].value = '';
                cancleButtons[i].classList.add('visibility-hidden');
                event.stopPropagation();
            })
        }
    }

    submitEvent() {
        const submitButton: HTMLElement = document.getElementById('submit') as HTMLElement;
        submitButton.addEventListener('click', () => {
            this.saveDocument();
            console.log(this.talentSellSaveData);
        });
    }

    render() {
    }
}