import View from '../core/view'
import { ListOptions } from '../types'
import { SellListApi } from '../core/api'
import { PRODUCT_LIST_URL } from '../config'

export default class SellListView extends View {
    listOptions: ListOptions;
    sellListContainer: HTMLElement;
    productAmount: HTMLElement;
    constructor(containerId: string, amountId: string) {
        super();
        const containerElement = document.getElementById(containerId);
        const amountElement = document.getElementById(amountId);

        if (!containerElement || !amountElement) {
            throw 'sell-list 컨테이너가 없습니다.';
        }
        this.sellListContainer = containerElement;
        this.productAmount = amountElement;
        this.listOptions = {
            page: 1,
            amount: 20,
            categoryId: '',
            locationId: '',
            sortOption: 'modifiedDate'
        };
        this.addEvent();
        console.log('sell-list-view');
    }

    addEvent() {
        this.categoryEvent();
        this.locationEvent();
        this.searchOptionEvent();
        this.viewOptionEvent();
    }

    categoryEvent() {
        const items: NodeListOf<HTMLElement> = document.querySelectorAll('.category-item');
        items.forEach(item => item.addEventListener('click', () => {
            if (item.classList.contains('category-choice')) {
                return;
            }
            const items: NodeListOf<HTMLElement> = document.querySelectorAll('.category-item');
            items.forEach(item => item.classList.remove('category-choice'));
            item.classList.add('category-choice');
            this.listOptions.categoryId = this.categoryConvertor(item.innerText);
        }));
    }

    locationEvent() {
        const item: HTMLSelectElement = document.getElementById('location-city') as HTMLSelectElement;
        item.addEventListener('change', () => this.listOptions.locationId = this.locationConvertor(item.options[item.selectedIndex].text));
    }

    searchOptionEvent() {
        const applyBtn: HTMLElement | null = document.getElementById('search-option-apply-button');
        if (applyBtn) {
            applyBtn.addEventListener('click', () => this.render());
        }
    }

    viewOptionEvent() {
        const items: NodeListOf<HTMLElement> = document.querySelectorAll('.sort-option-item');
        items.forEach(item => item.addEventListener('click', () => {
            const itemClasses: DOMTokenList = item.classList;
            if (itemClasses.contains('sort-choice')) {
                return;
            }
            const items: NodeListOf<HTMLElement> = document.querySelectorAll('.sort-option-item');
            items.forEach(item => item.classList.remove('sort-choice'));
            itemClasses.add('sort-choice');
            this.listOptions.sortOption = this.sortConvertor(item.innerText);
            this.render();
        }));
    }

    categoryConvertor(categort: string): string {
        switch (categort) {
            case '전체':
                return '';
            case '디자인':
                return '01';
            case 'IT프로그래밍':
                return '02';
            case '통역 번역':
                return '03';
            case '마케팅':
                return '04';
            case '영상/편집':
                return '05';
            case '레슨':
                return '06';
            case '스포츠':
                return '07';
            case '문서작업':
                return '08';
            default:
                return '';
        }
    }

    locationConvertor(categort: string): string {
        switch (categort) {
            case '전체':
                return '';
            case '서울':
                return '01';
            case '부산':
                return '02';
            case '대구':
                return '03';
            case '인천':
                return '04';
            case '광주':
                return '05';
            case '대전':
                return '06';
            case '울산':
                return '07';
            case '세종':
                return '08';
            case '경기':
                return '09';
            case '강원':
                return '10';
            case '충청북도':
                return '11';
            case '충청남도':
                return '12';
            case '전라북도':
                return '13';
            case '전라남도':
                return '14';
            case '경상북도':
                return '15';
            case '경상남도':
                return '16';
            case '제주':
                return '17';
            default:
                return '';
        }
    }

    sortConvertor(option: string): string {
        switch (option) {
            case '최신순':
                return 'modifiedDate';
            case '가격 높은 순':
                return 'expensive';
            case '가격 낮은 순':
                return 'cheap';
            case '평점 높은 순':
                return 'review';
            default:
                return 'modifiedDate';
        }
    }

    async render() {
        const api = new SellListApi(PRODUCT_LIST_URL
            .replace('@type', 'talent-sell')
            .replace('@page', String(this.listOptions.page))
            .replace('@amount', String(this.listOptions.amount))
            .replace('@category', this.listOptions.categoryId)
            .replace('@location', this.listOptions.locationId)
            .replace('@sort', this.listOptions.sortOption));
        const data = await api.getData();
        data.forEach(item => {
            const { productId, title, image, price, reviewCount, averageStarScore } = item;
            this.addHtml(
                `
            <a href="/product/detail/${productId}" class="sell-list-content">
                <div class="img-wrapper"><img src="${image}" alt="컨텐츠 이미지"></div>
                <div class="sell-list-content-title">${title}</div>
                <div class="sell-list-content-star">⭐️ ${averageStarScore} |</div>
                <div class="sell-list-content-review">${reviewCount}개의 평가</div>
                <div class="sell-list-content-price">${price}원</div>
            </a>
            `);
        })
        if(data.length%4 != 0) {
            for(let i=0; i<data.length%4; i++) {
                this.addHtml(`<div class="sell-list-content"></div>`);
            }
        }
        this.sellListContainer.innerHTML = this.getHtml();
        this.productAmount.innerHTML = `${data.length}개의 게시물`;
    }
}