import View from '../core/view'

export default class ProductDetailView extends View{
    imageContainer: HTMLImageElement;
    constructor(containerId: string) {
        super();
        const containerElement = document.getElementById(containerId) as HTMLImageElement;

        if(!containerElement) {
            throw 'image 컨테이너가 없습니다.';
        }
        this.imageContainer = containerElement;

        this.addEvent();
    }

    addEvent(): void {
        const images: NodeListOf<HTMLImageElement> = document.querySelectorAll("div.small-image-view > img");
        images.forEach((image) => image.addEventListener("mouseenter", (event) => {
            if(this.imageContainer) {
                this.imageContainer.src = image.src;
            }
        }))
    }

    render(): void {
    }
}