import {Mypage, ProductPreview} from '../types'

class Api {
    protected url;

    constructor(url: string) {
        this.url = url;
    }

    protected async getRequest<Response>(): Promise<Response> {
        const response = await fetch(this.url, {
            method: 'GET',
            headers: {
                "Content-Type": "application/json",
            }
        })
        return await response.json() as Response;
    }

    protected async postRequest<Response>(method: string, body: Object): Promise<Response> {
        const response = await fetch(this.url, {
            method: method,
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                ...body
            })
        })
        return await response.json() as Response;
    }
}

class MypageApi extends Api {
    constructor(url: string) {
        super(url);
    }

    async getData(): Promise<Mypage[]> {
        return this.getRequest<Mypage[]>();
    }
}

export class SellListApi extends Api {
    constructor(url: string) {
        super(url);
    }

    async getData(): Promise<ProductPreview[]> {
        return this.getRequest<ProductPreview[]>();
    }
}